import { Inject, Injectable } from '@angular/core';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Device } from './device';

@Injectable({
  providedIn: 'root'
})
export class DeviceStorageService {

  constructor(@Inject(LOCAL_STORAGE) private storage: StorageService) { }

  KEY = "_devices"

  public storeDevice = (device: Device): void => {
    const storedDevices: Array<string> = this.storage.get(this.KEY) || [];
    storedDevices.push(JSON.stringify(device));
    this.storage.set(this.KEY, storedDevices);
  }

  public getDevices = (): Array<Device> => {
    const rawDevices: Array<string> = this.storage.get(this.KEY);
    const parsedDevices: Array<Device> = rawDevices.map(device => {
      const obj = JSON.parse(device);
      return new Device(obj.id, obj.name, obj.wattage, obj.avgUsagePerDay, obj.avgUagePerMonth);
    });
    return parsedDevices;
  }

}
